<script>
import "@/assets/fonts.css";
import VInput from "./components/v-input.vue";
import CustomInput from "./components/custom-input.vue";
import VModal from "./components/v-modal.vue";
import FormHeader from "./components/form-header.vue";
import FormFooter from "./components/form-footer.vue";
// import TitH2 from "./components/tit-h2.vue";
import TitH3 from "./components/tit-h3.vue";
import VText from "./components/v-text.vue";
import Step3070 from "./components/step-30-70.vue";
import Step5050 from "./components/step-50-50.vue";
import VLink from "./components/v-link.vue";
import VSelect from "./components/v-select.vue";
import VButton from "./components/v-button.vue";

export default {
  components: {
    VInput,
    CustomInput,
    VModal,
    FormHeader,
    FormFooter,
    // TitH2,
    TitH3,
    VSelect,
    VButton,
    VText,
    Step3070,
    Step5050,
    VLink,
  },
  data() {
    return {
      optionArray: [
        { text: "option1", id: 0 },
        { text: "option2", id: 1 },
        { text: "option3", id: 2 },
      ],
      conditions1Array: [
        [0.9, 1, 1, 1],
        [0.5, 0.5, 0.5, 1],
      ],
      conditions2Array: [
        [0.9, 1, 1, 1],
        [0.5, 0.7, 0.9, 1],
      ],
      conditions3Array: [
        [0.95, 1, 1, 1],
        [0.7, 0.9, 1, 1],
      ],
      conditionArray: [],
      select: "",
      currentStep: 0,
      upadateTelInput: false,
      upadateEmailInput: false,
      pageWidth: window.innerWidth,
      amountSteps: 7,
      modalVisible: true,
      registerAddresInputsVisible: false,
      lifeAddresInputsVisible: false,
      INNplaceholder:
        window.innerWidth > 768
          ? "Идентификационный номер налогоплательщика (ИНН)"
          : "ИНН",
    };
  },
  methods: {
    addStep() {
      if (this.currentStep < this.amountSteps) {
        this.currentStep += 1;
      }
    },
    decStep() {
      if (this.currentStep > 0) {
        this.currentStep -= 1;
      }
    },
    disabledPatronymic(e) {
      this.formReg.patronymicExist = !e.checked;
      if (e.checked) {
        this.formReg.patronymic = "";
      }
    },
    visibleRegisterAddresInputs() {
      if (this.registerAddresInputsVisible) {
        this.registerAddresInputsVisible = false;
      } else {
        this.registerAddresInputsVisible = true;
      }
    },
    visibleLifeAddresInputs() {
      if (this.lifeAddresInputsVisible) {
        this.lifeAddresInputsVisible = false;
      } else {
        this.lifeAddresInputsVisible = true;
      }
    },
  },
  mounted() {
    this.conditionArray = this.conditions1Array;
  },
};
</script>

